import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
  Link,
  NavLink
} from "react-router-dom";
import Logo from '../assets/images/logo.png'

import { signOutUser } from '../actions/actions';

class Header extends React.Component {
  signOut = (e) => {
    e.preventDefault();
    window.clientApplication.logout();
    this.props.signOutUser();
  }

  render () {
    return (
      <header>
        <Link to="/">
          <img src={Logo} alt="Projul Logo" />
        </Link>
          { this.props.current_user && (
            <ul className={"flex-center" + (!this.props.current_user || this.props.current_user.company_setup || this.props.current_user.user_setup?"":" menu-placeholder")}>
              <li><NavLink exact to="/dashboard"><i className="fas fa-home"></i><span>dashboard</span></NavLink></li>
              <li><NavLink to="/projects"><i className="fas fa-toolbox"></i><span>projects</span></NavLink></li>
              <li><NavLink exact to="/schedule"><i className="far fa-calendar-alt"></i><span>schedule</span></NavLink></li>
              <li><NavLink exact to="/leads"><i className="fas fa-phone"></i><span>leads</span></NavLink></li>
            </ul>
          )}
        <nav>
          { this.props.current_user && (
            <ul>
              <li><a href="#sign-out" onClick={this.signOut}><i className="fas fa-sign-out-alt"></i><span>sign out</span></a></li>
            </ul>
          )}
        </nav>
      </header>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    current_user: state.state.current_user
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({signOutUser}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);
