import React from 'react';
import Project from './project';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {selectJob} from '../actions/actions'

class ProjectList extends React.Component {
  // constructor(props) {
  //   super(props);
  // }


  selectProject = (id) => {
    return (_e)=>{
      this.props.selectJob(id, null);
    };
  }

  render() {
    let projects = [];
    if (this.props.all_project_data) {
      let counter = 0;
      for (let p in this.props.all_project_data) {
        projects.push((
          <div
            key={"project-"+this.props.all_project_data[p].id}
            className={"card" + (counter<5?" d"+(counter+1):" d5")}
            onClick={this.selectProject(this.props.all_project_data[p].id)}
          >
            <Project
              id={this.props.all_project_data[p].id}
              project_data={this.props.all_project_data[p]}
            />
          </div>
        ));
        counter++;
      }
    }

    return (
      <div>
        {projects}
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_project_data: state.state.all_project_data
  }
}
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectJob}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(ProjectList);
