import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { updateLeadsData } from '../actions/actions';

const heat_map = [
  "white",
  "#fff3e9",
  "#ffe5cf",
  "#ffd7b6",
  "#ffc99c",
  "#ffbb83",
  "#ffad69",
  "#ff9f50",
  "#ff9136",
];

const node_heat = [
  "l-0",
  // "l-1",
  // "l-2",
  // "l-3",
  // "l-4",
  "l-5",
  "l-5",
  "l-5",
  // "l-6",
  // "l-7",
  // "l-8",
  // "l-9",
  "l-10",
];

const heat_map_color = {
  new_leads: "l-10",
  pending_leads: "l-0",
  scheduled_leads: "l-10",
  estimate_leads: "l-10",
  approved_leads: "l-10",
  rejected_leads: "l-0"
}

const error_color = "l-0";
const base_color = "l-10";

const max_heat = {
  new: 0,
  scheduled: 10,
  pending: 3,
  estimate: 4,
  approved: 0,
  rejected: 0,
};

class LeadChart extends React.Component {

  render() {
    let leads = {
      new_leads: [],
      pending_leads: [],
      scheduled_leads: [],
      estimate_leads: [],
      approved_leads: [],
      rejected_leads: []
    }
    var total_leads = 0;
    for (let l in this.props.all_leads_data) {
      switch (this.props.all_leads_data[l].status) {
        case 'new':
          leads.new_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'pending':
          leads.pending_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'scheduled':
          leads.scheduled_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'estimate':
          leads.estimate_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'approved':
          leads.approved_leads.push({...this.props.all_leads_data[l]});
          break;
        case 'rejected':
          leads.rejected_leads.push({...this.props.all_leads_data[l]});
          break;
        default:
          leads.other_leads.push({...this.props.all_leads_data[l]});
      }
      total_leads++;
    }

    const most_leads = Math.max(
      leads.new_leads.length,
      leads.pending_leads.length,
      leads.scheduled_leads.length,
      leads.estimate_leads.length,
      leads.approved_leads.length,
      leads.rejected_leads.length
    )

    const heat_map_ranges = {
      new_leads: {range:20},
      scheduled_leads: {range:30},
      pending_leads: {range:4,flip:true},
      estimate_leads: {range:5},
      approved_leads: {range:15},
      rejected_leads: {range:4,flip:true},
    }

    let nodes = {};
    for (let l in leads) {
      // leads[l].sort(function(a, b){
      //   var x = a.index;
      //   var y = b.index;
      //   if (x < y) {return -1;}
      //   if (x > y) {return 1;}
      //   return 0;
      // });
      nodes[l] = (
        <div className="chart-node" key={l}>
        <div
          className={(heat_map_ranges[l].flip&&(leads[l].length > heat_map_ranges[l].range)) ?  error_color : base_color//heat_map_color[l]
          //   heat_map_ranges[l] ? node_heat[
          //   (heat_map_ranges[l].flip ?
          //     Math.max(Math.min(node_heat.length - (Math.round(((leads[l].length)/heat_map_ranges[l].range) * 10)- 1),node_heat.length - 1), 0) :
          //     Math.min(Math.round(((leads[l].length)/heat_map_ranges[l].range) * 10),node_heat.length-1)
          //   )
          // ] : ""
        }
          style={{
            // width: (leads[l].length / total_leads)*50 + "vmin",
            // height: (leads[l].length / total_leads)*50 + "vmin"
            transform: "scaleY(" + ((leads[l].length / total_leads)*5) + ") scaleX(4) rotate(45deg)"
          }}
        ></div>
        { (heat_map_ranges[l].flip && leads[l].length > heat_map_ranges[l].range) &&
          (<i className="fas fa-exclamation-triangle"></i>)
        }
        </div>
      );
    }

    return (
      <div className="lead-chart">
        <div className="bar"></div>
        <div className="chart">
          {nodes.new_leads}
          {nodes.scheduled_leads}
          {nodes.pending_leads}
          {nodes.estimate_leads}
          {nodes.approved_leads}
          {nodes.rejected_leads}
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateLeadsData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LeadChart);
