import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {selectJob} from '../actions/actions'

import Project from './project';
import JobDetails from './job-details';

class ProjectEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loaded: false,
      last_selected_project: this.props.selected_project
    };
  }
  componentDidMount() {
    // setTimeout(()=>{
    //   var projects_by_id = {};
    //   for (var p=0;p<ProjectData.length; p++) {
    //     projects_by_id[ProjectData[p].id]=ProjectData[p];
    //   }
    //   this.setState({
    //     loaded: true,
    //     projects_by_id: projects_by_id
    //   });
    // }, 50);
  }

  componentDidUpdate(prevProps) {
    if (this.props.selected_project !== prevProps.selected_project) {
      this.selectProject(this.props.selected_project, this.props.selected_job);
    }
  }

  componentWillUnmount () {
    window.clearTimeout(this.transition_finish);
    window.clearTimeout(this.transition);
  }

  selectProject (id, job_id) {
    this.transition = setTimeout(()=>{
      this.setState({
        transition_last_selected_project: false,
      });
    }, 50);
    this.transition_finish = setTimeout(()=>{
      this.setState({
        last_selected_project: this.props.selected_project
      });
    }, 1500);
    this.setState({
      transition_last_selected_project: true
    });
  }

  handleCloseButton = (e) => {
    this.props.selectJob(null, null);
  }

  render() {
    let color;
    if (this.props.all_project_data && this.props.all_project_data[this.props.selected_project]) {
      color = this.props.all_project_data[this.props.selected_project].color || "black";
    }
    return (
      <div id="project-details" className={(this.props.selected_project && this.props.all_project_data?" show":"")}>
        <div className="card">
          <div className="header-color" style={{
            backgroundColor: color
          }} />

          <div className="button-menu">
            <button onClick={this.handleCloseButton} className="icon-button"><i className="fas fa-times"></i></button>
          </div>

          <div className={ "selected_project" +
            (this.state.transition_last_selected_project ? " transition_last_selected" : "")
          }>
            {this.state.last_selected_project && (
              <Project
                id={this.state.last_selected_project}
                last_selected={true}
                project_data={this.props.all_project_data[this.state.last_selected_project]}
              />
            )}
            { this.props.selected_project &&
              (<Project
                id={this.props.selected_project}
                project_data={this.props.all_project_data[this.props.selected_project]}
              />)
            }
          </div>
          <JobDetails />
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    selected_job: state.state.selected_job,
    selected_project: state.state.selected_project,
    all_project_data: state.state.all_project_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({selectJob}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(ProjectEditor);
