import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { updateLeadsData } from '../actions/actions';

import LeadKanban from './lead-kanban';
import LeadChart from './lead-chart';

import {LeadList} from '../utilities/data'

class PageLeads extends React.Component {
  state = {
    loaded: false
  };
  componentDidMount() {
    setTimeout(()=>{
      this.props.updateLeadsData(LeadList);
      this.setState({loaded: true});
    }, 1000);
  }
  render() {
    return (
      <section id="page-leads" className={this.state.loaded?"loaded":"loading"}>
        <div className="load-background"></div>
        <div className="card f-nogrow">
          <LeadChart />
        </div>
        <div className="card d1 f-grow">
          <LeadKanban />
        </div>
      </section>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateLeadsData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(PageLeads);
