import React from 'react'
import { connect } from 'react-redux';

import Scheduler from './scheduler'

class PageSchedule extends React.Component {
  state = {
    loaded: false
  };
  componentDidMount() {
    setTimeout(()=>{
      this.setState({loaded: true});
    }, 1);
  }
  render() {
    return (
      <section id="page-schedule" className={this.state.loaded?"loaded":"loading"}>
        <Scheduler className="card" />
      </section>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_users_data: state.state.all_users_data,
    all_project_data: state.state.all_project_data
  }
}


export default connect(mapStateToProps)(PageSchedule);
