import React from 'react'
import Axios from '../utilities/api'
import {applicationConfig} from '../config/config'

function callApi() {
  window.clientApplication.acquireTokenSilent(applicationConfig.b2cScopes).then(function (accessToken) {
      callApiWithAccessToken(accessToken);
  }, function (error) {
    window.clientApplication.acquireTokenPopup(applicationConfig.b2cScopes).then(function (accessToken) {
        callApiWithAccessToken(accessToken);
    }, function (error) {
        console.log("Error acquiring the access token to call the Web api:\n" + error);
    });
  })
}
function callApiWithAccessToken(accessToken) {
    // Call the Web API with the AccessToken
    Axios({
        type: "GET",
        url: applicationConfig.webApi,
        headers: {
            'Authorization': 'Bearer ' + accessToken,
        },
    }).then(function (data) {
        console.log("Web APi returned:\n" + JSON.stringify(data));
    })
    .catch(function (error) {
        console.log("Error calling the Web api:\n" + error);
    })
}


class PageDashboard extends React.Component {
  state = {
    loaded: false
  };
  componentDidMount() {
    setTimeout(()=>{
      this.setState({loaded: true});

      callApi();

    }, 50);
  }
  render() {
    return (
      <section id="page-dashboard">
        <div className={this.state.loaded?"loaded":"loading"}>
          <div className="card d1">
          </div>
          <div className="card d2">
          </div>
        </div>
        <div className={this.state.loaded?"loaded":"loading"}>
          <div className="card d3">
          </div>
        </div>
      </section>
    );
  }
}

export default PageDashboard
