import DefaultState from '../store/default-state'

const reducers = (state = DefaultState, action) => {
  switch (action.type) {
    case 'EXAMPLE':
      return {
        ...state,
        examplePropOne: 'newPropOne'
      }
    case 'EXAMPLE-TWO':
      return {
        ...state,
        examplePropTwo: action.sampleData + action.moreData
      }
    case 'SETUP-COMPANY':
      return {
        ...state,
        current_user: {...(state.current_user||{}), company_setup:true}
      }
    case 'SETUP-USER':
      return {
        ...state,
        current_user: {...(state.current_user||{}), user_setup:true}
      }
    case 'SIGNIN-USER':
      return {
        ...state,
        current_user: action.user_data
      }
    case 'SIGNOUT-USER':
      return {
        ...state,
        current_user: null
      }
    case 'SELECT-JOB':
      return {
        ...state,
        selected_project: action.project_id,
        selected_job: action.job_id
      }




    case 'UPDATE-ALL-USERS-DATA':
      return {
        ...state,
        all_users_data: action.all_users_data
      }
    case 'UPDATE-USERS-DATA':
      return {
        ...state,
        all_users_data: {
          ...state.all_users_data,
          ...action.users_data
        }
      }




    case 'UPDATE-ALL-LEADS-DATA':
      return {
        ...state,
        all_leads_data: action.all_leads_data
      }
    case 'UPDATE-LEADS-DATA':
      return {
        ...state,
        all_leads_data: {
          ...state.all_leads_data,
          ...action.leads_data
        }
      }



    case 'UPDATE-ALL-PROJECT-DATA':
      return {
        ...state,
        all_project_data: action.all_project_data
      }



    case 'UPDATE-ALL-JOB-DATA':
      return {
        ...state,
        all_job_data: action.all_job_data
      }
    case 'UPDATE-JOB-DATA':
      return {
        ...state,
        all_job_data: {
          ...state.all_job_data,
          ...action.job_data
        }
      }
    case 'EDIT-JOB':
      return {
        ...state,
        all_job_data: {
          ...state.all_job_data,
          [action.job_id]:{
            ...state.all_job_data[action.job_id],
            ...action.job_object
          }
        }
      }



    case 'UPDATE-ALL-TASK-DATA':
      return {
        ...state,
        all_task_data: action.all_task_data
      }
    case 'UPDATE-TASK-DATA':
      return {
        ...state,
        all_task_data: {
          ...state.all_task_data,
          ...action.task_data
        }
      }
    case 'ADD-TASK':
      let job_data = {...state.all_job_data};
      let project_data = {...state.all_project_data};
      // add to job
      if (action.job_id) {
        job_data[action.job_id].tasks.push(action.task_id);
      }
      // add to project
      if (action.project_id) {
        project_data[action.project_id].tasks.push(action.task_id);
      }
      return {
        ...state,
        all_project_data: project_data,
        all_job_data: job_data,
        all_task_data: {
          ...state.all_task_data,
          [action.task_id]:action.task_object
        }
      }
    case 'EDIT-TASK':
      return {
        ...state,
        all_task_data: {
          ...state.all_task_data,
          [action.task_id]:{
            ...state.all_task_data[action.task_id],
            ...action.task_object
          }
        }
      }
    case 'DELETE-TASKS':
      job_data = {...state.all_job_data};
      project_data = {...state.all_project_data};
      let all_task_data = {...state.all_task_data};

      for (let i=0; i<action.task_ids.length; i++) {
        let task = all_task_data[action.task_ids[i]];
        //if the task is part of a job
        if (task.job_id) {
          for (let t=0; t<job_data[task.job_id].tasks.length; t++) {
            if (job_data[task.job_id].tasks[t] === action.task_ids[i]) {
              job_data[task.job_id].tasks.splice(t, 1);
            }
          }
        }
        //if the task is part of a project
        if (task.project_id) {
          for (let t=0; t<project_data[task.project_id].tasks.length; t++) {
            if (project_data[task.project_id].tasks[t] === action.task_ids[i]) {
              project_data[task.project_id].tasks.splice(t, 1);
            }
          }
        }
        //delete the task itself
        delete all_task_data[action.task_ids[i]];
      }
      return {
        ...state,
        all_job_data: job_data,
        all_project_data: project_data,
        all_task_data: all_task_data
      }
    case 'COMPLETE-TASK':
      const completed_task = {
        ...state.all_task_data[action.task_id],
        completed: true
      }
      return {
        ...state,
        all_task_data: {
          ...state.all_task_data,
          [action.task_id]:completed_task
        }
      }


    default: return state;
  }
}

export default reducers;
