export const LeadList = {};

const lead_status = [
  "new",
  "scheduled",
  "pending",
  "estimate",
  "approved",
  "rejected"
]
const max_heat = {
  new: 3,
  scheduled: 7,
  pending: 10,
  estimate: 8,
  approved: 6,
  rejected: 3,
};
const number_of_leads = {
  0:20,
  1:10,
  2:5,
  3:5,
  4:6,
  5:4,
};

function GenerateLeadsData () {
  let id;
  let name_counter = 0;
  const today = new Date();
  for (var i in number_of_leads) {
    for (var l=0; l<number_of_leads[i]; l++) {
      id = "lead_"+Math.random();
      const status_value = i;//Math.floor(Math.random()*lead_status.length);
      // const added_date = new Date(today.getFullYear(), today.getMonth(), today.getDate()-Math.floor(Math.random()*5*status_value));
      let dates = {};
      let last_date = today;
      for (let l=status_value; l>=0; l--) {
        last_date = new Date(last_date.getFullYear(), last_date.getMonth(), last_date.getDate()-Math.floor((Math.random()*max_heat[lead_status[status_value]])));
        dates[lead_status[l]] = last_date;
      }
      let name = name_counter<random_names.length ? random_names[name_counter] : random_names[Math.floor(Math.random()*random_names.length)];
      let name_list = name.split(" ");
      LeadList[id] = {
        id: id,
        index: l,
        new_date: dates.new,
        scheduled_date: dates.scheduled,
        pending_date: dates.pending,
        estimate_date: dates.estimate,
        approved_date: dates.approved,
        rejected_date: dates.rejected,
        status: lead_status[status_value],
        first_name: name_list[0],
        last_name: name_list[1]
      }
      name_counter++
    }
  }
}


GenerateLeadsData();
