import React from 'react';
import {
  Route
} from 'react-router-dom'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {updateAllProjectData, selectJob} from '../actions/actions'

import ProjectList from './project-list';
import ProjectEditor from './project-editor';

import {URLString} from '../utilities/string';
import {ProjectData} from '../utilities/data';

class PageProjects extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loaded: false
    };
  }

  componentDidMount() {
    this.temp_load_data = setTimeout(()=>{
      var projects_by_id = {};
      const location_array = this.props.location.pathname.split("/");
      let selected_id;

      for (var p=0;p<ProjectData.length; p++) {
        projects_by_id[ProjectData[p].id]=ProjectData[p];
        if (URLString(ProjectData[p].name) === location_array[2]) {
          selected_id = ProjectData[p].id;
        }
      }
      this.props.updateAllProjectData(projects_by_id);
      if (location_array[2]) {
        this.props.selectJob(selected_id, null);
      }

      setTimeout(()=>{
        this.setState({
          loaded: true,
        });
      }, 1);
    }, 2000);
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_load_data);
  }

  componentDidUpdate(prevProps) {
    if (this.props.selected_project !== prevProps.selected_project) {
      this.props.history.push("/projects" + (this.props.selected_project ? "/" + URLString(this.props.all_project_data[this.props.selected_project].name) : ""));
    }
  }

  render() {
    return (
      <section id="page-projects" className={"projects " + (this.state.loaded?"loaded":"loading")}>
        <div className="load-background"></div>
        <Route path="/projects/:p" component={ProjectEditor} />
        <Route path="/projects" component={ProjectList} />
      </section>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    selected_project: state.state.selected_project,
    all_project_data: state.state.all_project_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateAllProjectData, selectJob}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(PageProjects);
