import React from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {updateTaskData, editJob, completeTask, deleteTasks, editTask, addTask} from '../actions/actions'

import ModalWarning from './modal-warning'
import {TasksById} from '../utilities/data'
import {Content} from '../utilities/content'
import {ArrayMove} from '../utilities/array'

const DeleteAllTasksWarning = Content[window.loc].DeleteAllTasksWarning;
const MarkAllTasksCompleteWarning = Content[window.loc].MarkAllTasksCompleteWarning;

class TaskList extends React.Component {
  state = {
    loaded: false,
    add_task_text: "",
    temp_edit_text: "",
    currently_editing: 0,
    all_complete: false,
    selected_job: null
  };

  callAPI () {
    this.temp_api_call = setTimeout(()=>{
      var tasks_by_id = {};

      for (var i in TasksById) {
        for (var t=0;t<this.props.all_job_data[this.props.selected_job].tasks.length; t++) {
          if (this.props.all_job_data[this.props.selected_job].tasks[t] === TasksById[i].id) {
            if (!this.props.all_task_data[TasksById[i].id]) {
              tasks_by_id[TasksById[i].id] = {...TasksById[i]};
            }
          }
        }
      }

      this.props.updateTaskData(tasks_by_id);

      this.setState({
        loaded: true,
        selected_job: this.props.selected_job
      })
    }, 50);
  }

  componentDidMount () {
    if (this.props.all_job_data[this.props.selected_job]) {
      this.callAPI();
    }
  }
  componentDidUpdate(prevProps) {
    if (this.props.selected_job) {
      if (this.props.selected_job !== prevProps.selected_job) {
        this.callAPI();
        this.setState({
          loaded: false
        });
      }
    }
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_api_call);
  }


  checkIfAllComplete(job_tasks) {
    let completed = true;
    for (let t=0; t<job_tasks.length; t++) {
      const task = this.props.all_task_data[job_tasks[t]];
      if (!task.completed) {
        completed = false;
        break;
      }
    }
    return completed && !!job_tasks.length;
  }

  handleTaskClick = (id) => {
    return (_e) => {
      if (this.state.currently_editing !== id) {
        let task = {...this.props.all_task_data[id]};
        task.completed = !task.completed;
        this.props.editTask(id, {...task});
        // this.setState({
        //   tasks: tasks
        // });
      }
    }
  }

  handleCompleteAll = () => {
    const job_tasks = this.props.all_job_data[this.state.selected_job].tasks;
    window.openModal((
      <ModalWarning
        message={(
          <div>
            <p>
              <span>{MarkAllTasksCompleteWarning.message_1}</span>
              <strong>{this.props.all_job_data[this.state.selected_job].name}</strong>
              <span>{MarkAllTasksCompleteWarning.message_2}</span>
            </p>
            <p>{MarkAllTasksCompleteWarning.message_3}</p>
          </div>
        )}
        cancel={MarkAllTasksCompleteWarning.cancel}
        confirm={MarkAllTasksCompleteWarning.confirm}
        handleConfirm={()=>{
          let tasks = {};
          for (var t=0; t<job_tasks.length; t++) {
            let task = this.props.all_task_data[job_tasks[t]];
            task.completed = true;
            tasks[task.id] = task;
          }
          this.props.updateTaskData(tasks);
        }}
        handleCancel={()=>{}}
      />
    ));
  }

  handleIncompleteAll = () => {
    let tasks = {};
    const job_tasks = this.props.all_job_data[this.state.selected_job].tasks;
    for (var t=0; t<job_tasks.length; t++) {
      let task = this.props.all_task_data[job_tasks[t]];
      task.completed = false;
      tasks[task.id] = task;
    }
    this.props.updateTaskData(tasks);
  }

  handleDeleteAll = () => {
    const job_tasks = this.props.all_job_data[this.state.selected_job].tasks;
    window.openModal((
      <ModalWarning
        message={(
          <div>
            <p>
              <span>{DeleteAllTasksWarning.message_1}</span>
              <strong>{this.props.all_job_data[this.state.selected_job].name}</strong>
              <span>{DeleteAllTasksWarning.message_2}</span>
            </p>
            <p>{DeleteAllTasksWarning.message_3}</p>
            <p>{DeleteAllTasksWarning.message_4}</p>
          </div>
        )}
        cancel={DeleteAllTasksWarning.cancel}
        confirm={DeleteAllTasksWarning.confirm}
        handleConfirm={()=>{
          let tasks = [...job_tasks];
          this.props.deleteTasks(tasks);
        }}
        handleCancel={()=>{}}
      />
    ));
  }

  handleDelete = (id) => {
    return (e)=>{
      e.preventDefault();
      e.stopPropagation();
      this.props.deleteTasks([id]);
      // delete tasks[id];
      // this.setState({
      //   tasks: tasks
      // });
    }
  }


  handleAddKeyPress = (e) => {
    if(e.key === 'Enter'){
      this.handleAddTask();
    }
  }

  handleAddChange = (e) => {
    this.setState({
      add_task_text: e.currentTarget.value
    });
  }

  handleAddTask = (e) => {
    if (this.state.add_task_text) {
      const id = "task_"+Math.random();
      let task = {
        id: id,
        name: this.state.add_task_text,
        completed: false
      };

      this.props.addTask(id, task, this.state.selected_job);

      this.setState({
        add_task_text: ""
      });
    }
  }


  handleEdit = (id, name) => {
    return (e) => {
      e.preventDefault();
      e.stopPropagation();

      this.setState({
        currently_editing: id,
        temp_edit_text: name
      });
    }
  }

  handleEditKeyPress = (e) => {
    if(e.key === 'Enter'){
      this.handleConfirmEdit(e);
    }
  }

  handleEditChange = (e) => {
    this.setState({
      temp_edit_text: e.currentTarget.value
    });
  }

  handleCancelEdit = () => {
    return (e) => {
      e.preventDefault();
      e.stopPropagation();

      this.setState({
        currently_editing: null
      });
    }
  }

  handleConfirmEdit = () => {
    return (e) => {
      e.preventDefault();
      e.stopPropagation();
      let id=this.state.currently_editing;

      this.props.editTask(id, {name: this.state.temp_edit_text});

      this.setState({
        currently_editing: null
      });
    }
  }

  updateTasks (tasks) {
    // this.props.all_job_data[this.state.selected_job].tasks = tasks;
    let progress = 0;
    let total_progress = 0;
    let tasks_by_id = {};
    for (let t=0; t<tasks.length; t++) {
      if (tasks[t].completed) {
        progress++;
      }
      tasks_by_id[tasks[t].id] = tasks[t];
    }
    total_progress = Math.round(100*(progress / tasks.length)) || 0;

    // this.props.updateJobProgress(this.state.selected_job, tasks, total_progress);
    this.props.updateTaskData(tasks_by_id);
    //UPDATE JOB

    this.setState({
      tasks: tasks,
      all_complete: total_progress === 100
    });
  }

  changeMode (mode) {
    this.setState({
      mode: mode
    });
  }

  getMenu () {
    const job_tasks = this.state.selected_job && this.state.loaded ? this.props.all_job_data[this.state.selected_job].tasks : [];
    const menu = (
      <div className="menu">
        <div>
          <div className="button-group">
            <input type="text" placeholder="task name" value={this.state.add_task_text} onChange={this.handleAddChange} onKeyPress={this.handleAddKeyPress} />
            <button onClick={(e)=>{this.handleAddTask(e)}}><i className="fas fa-plus"></i><span>add task</span></button>
          </div>
        </div>
        <div>
          {(!this.checkIfAllComplete(job_tasks) || !job_tasks.length) ?(
            <button onClick={(e)=>{this.handleCompleteAll(e)}} disabled={!job_tasks.length}><i className="fas fa-check-circle"></i><span>mark all as complete</span></button>
          ):(
            <button onClick={(e)=>{this.handleIncompleteAll(e)}}><i className="far fa-circle"></i><span>mark all as incomplete</span></button>
          )}
          <button onClick={(e)=>{this.handleDeleteAll()}} disabled={!job_tasks.length}><i className="fas fa-trash"></i><span>delete all</span></button>
        </div>
      </div>
    );
    return menu;
  }

  handleTaskDragStart = (e) => {
    this.dragged_id= e.target.dataset.id;
    this.dragged_index= e.target.dataset.index;
  }

  handleTaskDragEnd = (e) => {
    window.setStyle(``);
    if (this.drop_id) {
      this.handleTaskDrop(e);
    }
  }

  handleTaskDragOver = (e) => {
    e.preventDefault();
    if (e.currentTarget.dataset.id !== this.drop_id) {
      //To fix performance issues, rather than calling render, we change a stylesheet to apply styles
      if (parseInt(e.currentTarget.dataset.index, 10) >= parseInt(this.dragged_index,10)) {
        window.setStyle(`
          [data-id="` + e.currentTarget.dataset.id + `"]:after {
            bottom: 0 !important;
            opacity: 1 !important;
          }
        `);
      } else {
        window.setStyle(`
          [data-id="` + e.currentTarget.dataset.id + `"]:after {
            top: 0 !important;
            opacity: 1 !important;
          }
        `);
      }
      this.drop_id = e.currentTarget.dataset.id;
      this.drop_index = e.currentTarget.dataset.index;
    }
  }

  handleTaskDragLeave = (e) => {
    e.preventDefault();
  }

  handleTaskDrop = (e) => {
    e.preventDefault();
    let tasks = [...this.props.all_job_data[this.state.selected_job].tasks];
    let old_index = this.dragged_index;
    let new_index = this.drop_index;

    this.drop_id = null;
    window.setStyle(``);

    ArrayMove(tasks, parseInt(old_index,10), parseInt(new_index,10));

    this.props.editJob(this.state.selected_job, {tasks});
  }


  render() {
    let task_nodes = [];
    if (this.state.selected_job) {
      const job_tasks = this.props.all_job_data[this.state.selected_job].tasks;

      if (job_tasks && this.state.loaded) {
        let tasks = [...job_tasks];

        for (var t=0; t<tasks.length; t++) {
          const task = this.props.all_task_data[tasks[t]];
          if (task) {
            task_nodes.push((
              <li
                key={this.state.selected_job + "_" + task.id}
                className={
                  "task" +
                  (task.completed ? " completed" : "") +
                  (task.id === this.state.currently_editing ? " currently-editing" : "") +
                  (this.state.drop_id === task.id ? (" drop-"+this.state.drop_location) : "")
                }
                data-id={task.id}
                data-index={t}
                onClick={this.handleTaskClick(task.id)}

                draggable
                onDrag={this.handleTaskDragStart}
                onDragEnd={this.handleTaskDragEnd}

                onDragOver={this.handleTaskDragOver}
                onDragLeave={this.handleTaskDragLeave}
                onDrop={this.handleTaskDrop}
              >
                <h3>
                  <span className="icon-holder"><i className="fas fa-check-circle check"></i><i className="far fa-circle"></i></span>
                  {this.state.currently_editing === task.id ? (
                    <input className="" defaultValue={task.name} onChange={this.handleEditChange} onKeyPress={this.handleEditKeyPress} />
                  ):(
                    <span className="task-name">{task.name}</span>
                  )}
                </h3>
                {this.state.currently_editing === task.id ? (
                  <div>
                    <button className="icon-button" onClick={this.handleConfirmEdit(task.id)} title="Apply"><i className="fas fa-check"></i></button>
                    <button className="icon-button" onClick={this.handleCancelEdit(task.id)} title="Cancel"><i className="fas fa-times"></i></button>
                  </div>
                ):(
                  <div>
                    <button className="icon-button" onClick={this.handleEdit(task.id, task.name)} title="Edit Task"><i className="fas fa-edit"></i></button>
                    <button className="icon-button" onClick={this.handleDelete(task.id)} title="Delete Task"><i className="fas fa-trash"></i></button>
                  </div>
                )}
              </li>
            ));
          }
        }
      }
    }

    return (
      <div className={"task-editor" + (this.state.loaded?"":" loading")} >
        {this.getMenu()}
        <div className="task-list">
          <div className="load-background"></div>
          <ul>{task_nodes}</ul>
        </div>

      </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    all_task_data: state.state.all_task_data,
    all_job_data: state.state.all_job_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateTaskData, editJob, completeTask, deleteTasks, editTask, addTask}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(TaskList);
