import React from 'react'
// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

// import {selectJob} from '../actions/actions'

import Editor from './editor'
import Scheduler from './scheduler'
import TaskList from './task-list'
import {JobsById, TasksById} from '../utilities/data'

class JobDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loaded: false,
      show_content: true,
      detail_mode: "schedule",
      job_data: null
    }
  }

  callAPI (detail) {
    if (this.props.selected_job) {
      this.temp_api_call = setTimeout(()=>{
        this.setState({
          loaded: true
        });
        if (detail) {
          this.setState({
            detail_mode: JobsById[this.props.selected_job].start_date ? "task" : "schedule"
          });
        }
        if (this.state.show_content){
          this.setState({
            job_data: JobsById[this.props.selected_job]
          });
        } else {
          this.setState({
            temp_job_data: JobsById[this.props.selected_job]
          });
        }
      }, 30);
    }
  }

  componentDidMount () {
    this.callAPI(true);
  }

  componentDidUpdate (prevProps) {
    if (this.props.selected_job !== prevProps.selected_job) {
      this.setState({loaded: false, show_content: false});
      //API CALL HERE
      this.callAPI();

      this.show_content_animation = setTimeout(()=>{
        this.setState({
          show_content: true
        });
        if (this.state.loaded) {
          this.setState({
            job_data: this.state.temp_job_data,
            temp_job_data: null
          });
        }
      }, 500);
    }
  }

  componentWillUnmount () {
    window.clearTimeout(this.temp_api_call);
    window.clearTimeout(this.show_content_animation);
  }

  changeMode (mode) {
    this.setState({
      detail_mode: mode
    });
  }

  updateJobProgress = (job_id, tasks, progress) => {
    JobsById[job_id].progress = progress;
    let task_ids = [];
    let task;
    for (let t=0; t<tasks.length; t++) {
      task = tasks[t];
      task_ids.push(task.id);
      TasksById[task.id] = {...task};
    }
    JobsById[job_id].tasks = task_ids;
    this.props.updateJob(job_id, {...JobsById[job_id]});
  }

  render () {
    return (
      <div className={"job-detail-panel" +
        (this.state.loaded&&this.state.show_content?" loaded":"")
      }>
        { this.props.selected_job ? (
          this.state.job_data && (
            <div>
              <div>
                <header>
                  <h2>{this.state.job_data.name}</h2>
                  <div>
                    <button className={this.state.detail_mode === "schedule"?"selected":""} onClick={()=>{this.changeMode("schedule")}}>schedule job</button>
                    <button className={this.state.detail_mode === "task"?"selected":""} onClick={()=>{this.changeMode("task")}}>manage tasks</button>
                    <button className={this.state.detail_mode === "note"?"selected":""} onClick={()=>{this.changeMode("note")}}>take notes</button>
                  </div>
                </header>
                <div className="tab-panel">
                  <div className={this.state.detail_mode === "note"?"show":""}>
                    {
                      this.state.detail_mode === "note" &&
                      (<Editor
                        value="These are some notes!"
                        onChange={(value)=>{console.log(value);}}
                      />)
                    }
                  </div>
                  <div className={this.state.detail_mode === "task"?"show":""}>
                    {
                      this.state.detail_mode === "task"?
                        (<TaskList
                          selected_job={this.state.job_data.id}
                          default_trade={this.state.job_data.default_trade}
                         />)
                      :
                        []
                    }
                  </div>
                  <div className={this.state.detail_mode === "schedule"?"show":""}>
                    {
                      this.state.detail_mode === "schedule"&&
                      (<Scheduler
                        selected_job={this.state.job_data.id}
                        selected_job_data={this.state.job_data}
                        default_trade={this.state.job_data.default_trade}
                       />)
                    }
                  </div>
                </div>
              </div>

              <span className="no-task-selected load-screen">
                <h3>loading...</h3>
              </span>
            </div>
          )
        ):(
          <div className="no-task-selected">
            <i className="fas fa-hand-point-up"></i>
            <h3>select a job</h3>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    selected_job: state.state.selected_job
  }
}

// const mapDispatchToProps = (dispatch) => {
//   return bindActionCreators({selectJob}, dispatch);
// }

export default connect(mapStateToProps)(JobDetails);
