const mapStateToProps = (state) => {
  return {
    all_leads_data: state.state.all_leads_data
  }
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({updateLeadsData}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(LeadKanban);
